<template>
  <div>
    <van-nav-bar title="物流信息" fixed @click-left="$toPage(-1)">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left" />
    </van-nav-bar>
    <div class="head">
      <div class="head-img">
        <img src="../../assets/goodsDetails_icon/logistics.png" alt="" />
      </div>
      <div class="head-font">
        <!-- <p class="p2">运单编号：{{ gps_num.billno }}</p> -->
        <p class="p2">配送方式：一块物流</p>
      </div>
    </div>

    <van-steps direction="vertical" :active="0">
      <van-step v-for="(item, index) in gps_num" :key="index">
        <p>{{ item.status }}</p>
        <p>{{ item.des }}</p>
        <p>{{ item.date }}{{ item.time }}</p>
      </van-step>
    </van-steps>
  </div>
</template>

<script>
export default {
  data() {
    return {
      orderNo: 0,
      gps_num: []
    };
  },
  mounted() {
    this.orderNo = this.$route.query.orderNo;
    this.gps();
  },
  methods: {
    gps() {
      JNB.query({
        serverName:
          "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "OrderOptModule",
        method: "queryNodes",
        params: {
          orderno: this.orderNo,
          compid: this.$loadUserInfo().comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          this.gps_num = JSON.parse(result.data)
        }
      });
    }
  }
};
</script>

<style scoped>
.head-font {
  float: left;
  height: 100%;
  width: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
p {
  margin: 0;
  margin-top: 0px;
  font-weight: bold;
}
.p2 {
  width: 600px;
  font-size: 28px;
  font-weight: bold;
}
.head {
  background-color: white;
  padding-top: 100px;
  margin-bottom: 20px;
  width: 100%;
  height: 132px;
}
.head-img {
  height: 100%;
  width: 130px;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
}
img {
  height: 56px;
  width: 56px;
}
</style>